.footer {
  position: sticky;
  z-index: -1;
  bottom: 0;
  height: 200px;
  background-image: url(../images/footer-bg-mb.png);
  background-size: cover;
  background-position: center;

  @media (min-width: 744px) {
    height: 480px;
  }

  @media (min-width: 1260px) {
    height: 555px;
    background-image: url(../images/footer-bg.png);
  }
}
